<template>
    <div style="color: red; ">

        <div class="card">
            <div class="inner-box" id="card">
                <div class="card-front">
                    <h2>SLOTH-HELPER</h2>
                    <a-typography-title class="title">
                        Forgot Password
                    </a-typography-title>

                    <img src="../assets/logo.jpg">


                    <a-form :model="form" class="touch">
                        <a-form-item field="account" label="Email">
                            <a-input v-model="form.account" placeholder="please enter your email..." />
                        </a-form-item>
                        <a-form-item field="password" label="Verification Key">
                            <a-input-password v-model="form.password" placeholder="Enter your verification token" />
                        </a-form-item>

                        <a-form-item>
                            <a-button type="primary" status="success" @click="handleSubmit">Login</a-button>
                        </a-form-item>
                        <a-form-item>
                            <div style="color: red">{{ logininfo }}</div>
                        </a-form-item>


                        <a-space>
                            <router-link to="/">Go back to Login?</router-link>

                        </a-space>

                    </a-form>

                </div>
            </div>
        </div>
    </div>
</template>

<script>

import { inject, reactive, ref } from 'vue'
import { Modal } from '@arco-design/web-vue';

export default {
    name: "ForgotPasswordComponent",
    setup() {


        const logininfo = ref()
        const form = reactive({
            account: '',
            password: ''
        })
        const $axios = inject("$axios");
        const $router = inject("$router");
        const handleSubmit = () => {
            console.log(form.account);
            $axios({
                url: "/anki/vertify.do",
                method: "post",
                data: "email=" + form.account + "&password=" + form.password,
                headers: {

                }
            }).then((res) => {
                if (res.data.status == 200) {

                    $router.push("/reset");
                } else {
                    Modal.error({
                        title: 'Error',
                        content: res.data.info
                    });
                }
            })

        }

        return {
            form,
            handleSubmit,

            logininfo
        }
    },
}
</script>

<style scoped>
* {
    margin: 0;
    padding: 0;
}



.card {

    width: 350px;
    height: 500px;

    perspective: 1000px;
}

/* la rotation se passe ici sur inner */
.inner-box {
    margin-left: 450px;
    width: 100%;
    height: 100%;
    /*transform: rotateY(-180deg);*/
    transform-style: preserve-3d;
    transition: transform 3s;
}

.card-front {

    width: 100%;
    height: 100%;
    background-position: center;
    background-size: cover;
    background-image: linear-gradient(rgba(0, 0, 0, 0.904), rgba(0, 0, 0, 0.918)), url(../assets/logo.jpg);
    padding: 55px;
    box-sizing: content-box;
    backface-visibility: hidden;

}


.card h2 {
    font-size: normal;
    font-size: 24px;
    text-align: center;
    margin-bottom: 10px;

}

img {
    width: 40%;
    position: relative;
    left: 20px;
    justify-items: center;


}








.card .btn {
    margin-top: 0px;
}

.card a {
    color: white;
    text-decoration: none;
    display: block;
    text-align: center;
    font-size: 13px;
    margin-top: 8px;

}

.touch .text {
    padding-top: 15px;
    color: orange;
}
</style>